*{
    box-sizing: border-box;
    margin:0;
    padding:0;
    font-family:'Encode sans Expanded',
    sans-serif;
}

#hoverItem{
    transition: 0.3s;
    animation: ease-in-out;
}

#hoverItem:hover{
    transform: scale(1.04);
}

.cartImg{
    transition: 0.3s;
    animation: ease-in-out;
    object-fit:cover;
}
.cartImg:hover{
    transform: scale(1.04);
}

.cartH4{
    color: #4d4c4c;
    font-size: 16px;
    font-family: Rubik, ui-sans-serif;

}

#conditionsH4{
    color: #d90606;
    font-size: 18px;
    font-family: 'Mochiy Pop P One', sans-serif;

}

.cartH3{
    color: #4d4c4c;
    font-size: 15px;
    font-family: Rubik, ui-sans-serif;

}

.importantBody{
    margin-top: 12px;
    border: 1px solid red;
    padding: 5px;
    color: green;
    font-size: 10px;
}

#important{
    color: red;
}

.btnCss {
    border-radius: 50px;
    background: #01bf71;
    white-space: nowrap;
    color: #fff;
    font-size: 20px;
    outline: none;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
}

.btnCss:hover{
     transition: all 0.2s ease-in-out;
     background: #fff;
    color: #fff;
}
